.surprice-sacsses{
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50%;
  height: 100%;
}
.surprice-img{
  max-width: 100%;
}
.surprice {
  text-align: center;
  font-size: 20px;
  color: cornsilk;
  padding: 0 30px;

}
.surprice-data {
  text-align: center;
  font-size: 18px;
  color: cornsilk;
}
.link-panchingo {
padding: .5rem 1rem;
border-radius: 1.125rem;
background-color: blueviolet;
color: #fff;
text-decoration: none;
text-align: center;
display: block;
margin: 0 auto;
max-width: 70%;
}

.rules{
 margin-top: 80px;
}
.rules-list{
  display: flex;
  flex-direction: column;
}
.rules-description{
  padding: 5px 10px;
  display: flex;
  align-items: center;

}
.rules-box{
  display: flex;
  flex-direction: column;
  list-style-type:none;
}
.rules-title{
  text-align: center;
}
.rules-description_text {
  max-width: 70%;
}
.rules-description_number {
  color: #fff;
  background: rgb(131,58,180);
  background: linear-gradient(49deg, rgba(131,58,180,1) 0%, rgba(253,29,29,0.7467319691548495) 61%, rgba(252,176,69,0.2285246862416842) 100%);
  font-size: 30px;
  border-radius: 21%;
  max-width: max-content;
  padding: 7px;
  border: solid 2px blueviolet;
  margin-right: 10px;
}

.social-block {
  max-width: 100%;
  height: 30px;
  /* display: flex;
  justify-content: space-between;
  align-items: center;
  justify-content: space-around; */
  margin-bottom: 30px;
}
.social-vk{
  width: 100%;
  border-radius: 20px;
}
.social-internet{
  border: solid 2px blueviolet;
  border-radius: 50%;
}
.hiden{
  display: none;
}
.loading-mask {
  background-color: white;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}